        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-polygon</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:polygon.sync</span>=<span class="hljs-string">"polygon6"</span>&gt;</span>
