<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">polygon1</span>: {},
        <span class="hljs-attr">hierarchy1</span>: [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">-115</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">37.0</span> },
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">-115</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">32.0</span> },
