    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([
        <span class="hljs-keyword">this</span>.$refs.polygon1.createPromise,
        <span class="hljs-keyword">this</span>.$refs.polygon2.createPromise,
        <span class="hljs-keyword">this</span>.$refs.polygon3.createPromise,
        <span class="hljs-keyword">this</span>.$refs.polygon4.createPromise,
        <span class="hljs-keyword">this</span>.$refs.polygon5.createPromise,
        <span class="hljs-keyword">this</span>.$refs.polygon6.createPromise
      ]).then(<span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
        instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
