          { <span class="hljs-attr">lng</span>: <span class="hljs-number">-108.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">30.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">100000</span> }
