        <span class="hljs-keyword">this</span>.materia3 = Cesium.Color.ORANGE.withAlpha(<span class="hljs-number">0.5</span>)

        <span class="hljs-keyword">this</span>.material4 = Cesium.Color.BLUE.withAlpha(<span class="hljs-number">0.5</span>)

        <span class="hljs-keyword">this</span>.material5 = Cesium.Color.CYAN.withAlpha(<span class="hljs-number">0.5</span>)
        <span class="hljs-keyword">this</span>.hierarchy5 = Cesium.Cartesian3.fromDegreesArrayHeights([<span class="hljs-number">-90.0</span>, <span class="hljs-number">41.0</span>, <span class="hljs-number">0.0</span>, <span class="hljs-number">-85.0</span>, <span class="hljs-number">41.0</span>, <span class="hljs-number">500000.0</span>, <span class="hljs-number">-80.0</span>, <span class="hljs-number">41.0</span>, <span class="hljs-number">0.0</span>])

        <span class="hljs-keyword">this</span>.hierarchy6 = [
          { <span class="hljs-attr">lng</span>: <span class="hljs-number">-120.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">45.0</span> },
