<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 polygon 是否显示。</td>
</tr>
<tr>
<td>hierarchy</td>
<td>Object|Array</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 的 PolygonHierarchy 属性。</td>
</tr>
<tr>
<td>height</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 polygon 的高度。</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>extrudedHeight</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 拉伸高度。</td>
</tr>
<tr>
<td>extrudedHeightReference</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 拉伸的高度模式。 <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>stRotation</td>
<td>Number|Object</td>
<td><code v-pre>0.0</code></td>
<td><code v-pre>optional</code> 指定 polygon 纹理按正北方向逆时针旋转角度。</td>
</tr>
<tr>
<td>granularity</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> 指定每个经纬度之间的采样粒度。</td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 polygon 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'white'</code></td>
<td><code v-pre>optional</code> 指定 polygon 材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 polygon 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'black'</code></td>
<td><code v-pre>optional</code> 指定 polygon 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 polygon 轮廓线宽度。</td>
</tr>
<tr>
<td>perPositionHeight</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 polygon 是否使用每个位置的高度。</td>
</tr>
<tr>
<td>closeTop</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 polygon 拉伸出来的顶部是否闭合。</td>
</tr>
<tr>
<td>closeBottom</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 polygon 拉伸出来的底部是否闭合。</td>
</tr>
<tr>
<td>arcType</td>
<td>Number</td>
<td><code v-pre>1</code></td>
<td><code v-pre>optional</code> 指定 polygon 线条类型。<strong>NONE: 0, GEODESIC: 1, RHUMB: 2</strong></td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 polygon 是否投射或接收阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 polygon 随相机距离改变是否显示参数。<strong>结构：{ near: number, far: number }</strong></td>
</tr>
<tr>
<td>classificationType</td>
<td>Number</td>
<td><code v-pre>2</code></td>
<td><code v-pre>optional</code> 指定 polygon 贴对象模式。 <strong>TERRAIN: 0, CESIUM_3D_TILE: 1, BOTH: 2, NUMBER_OF_CLASSIFICATION_TYPES: 3</strong></td>
</tr>
<tr>
<td>zIndex</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 polygon 顺序，没有高度和拉伸高度时有效。</td>
</tr>
</tbody>
</table>
